
.changeThing {
	border-bottom: solid silver;
	border-top: solid silver;
}
.content {
	font-family: 'dingding';
	font-size: 24px;
	width: 100%;
	background: #F0F0F0;
	min-width: 800px;
}
.selectItems {
	border-right: solid #666;
	overflow: hidden;
	text-align: center;
	line-height: 80px;
	margin: 30px;
	border-radius: 30px;
	width: 200px;
	height: 80px;
	background-color: silver;
	cursor: pointer;
}
.smallItems {
	border-radius: 10px 0px;
	text-align: center;
	display: inline-block;
	width: 10em;
	cursor: pointer;
	padding: 5px;
	background-color: silver;
	margin: 0px 10px 10px 10px;
}
.adminSlide {
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
	cursor: pointer;
	margin-left: calc(50% - 37px);
	display: inline-block;
}
#addPage {
	position: fixed;
	background-color: #F0F0F0;
	height: 150px;
	bottom: 0;
	width: 100%;
	margin-left: -8px;
	overflow-Y: scroll;
	font-family: "微软雅黑";
	font-size: small;
	border-top: solid gray 1px;
	overflow: overlay;
}



